const { useCallback } = React
const e = React.createElement
import { useDropzone } from 'react-dropzone'
  
export default function App() {
  
  const onDrop = useCallback(acceptedFiles => {
    alert(acceptedFiles[0].name)
    console.log("Now you can do anything with"+
                " this file as per your requirement")
  }, [])
  
  const { getInputProps, getRootProps } = useDropzone({ onDrop })

  return e( 'div', { style: { display: 'block', width: 700, padding: 30 } }, [
    e( 'h4', {}, 'React-Dropzone Module Demo' )
    , e( 'div', { ...getRootProps() }, [
      e( 'input', { ...getInputProps() } )
      , e( 'p', {}, 'Click or drag files here' )
    ])
  ])
}

ReactDOM.render( React.createElement( App ), document.getElementById( 'root' ) )
